:root {
  --BBFbackground: #242424;
  --BBfontColor: #ffeede;
  --BBFmediumSize: 12px;
  font-size: var(--BBFmediumSize);
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  color: var(--BBfontColor);
  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media only screen and (min-width: 801px) {
  :root {
    --BBFmediumSize: 20px;
  }
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
aCCr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
button,
video {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
  line-height: initial;
  text-overflow: ellipsis;
}
html {
  background-color: var(--BBFbackground);
}
body {
  font-weight: normal;
  margin: 0 auto;
  padding: 0.5rem;
}
